  top: 100%; }

.mfp-bottom-up.mfp-wrap.mfp-ready .mfp-content {
  transition: all 0.5s cubic-bezier(0.25, 0.98, 0.26, 0.99);
  opacity: 1;
  top: 0; }

.mfp-bottom-up.mfp-wrap.mfp-removing .mfp-content {
  transition: all 0.5s cubic-bezier(0.25, 0.98, 0.26, 0.99);
  opacity: 0;
  top: 100%; }

/**
 * Top Down transition,
 */
.mfp-top-up.mfp-bg {
  opacity: 0;
  transition: all 0.3s ease-out; }

.mfp-top-up.mfp-bg.mfp-ready {
